<template>
  <div class="center-preview" :style="{
    width: '1200px',
    margin: '10px auto',
    position: 'relative',
    background: 'none',
    height: 'auto',
  }">
    <div class="title" :style="{
      padding: '20px 0',
      margin: '20px 0 20px 0',
      borderColor: '#FAFFE6',
      color: '#333',
      textAlign: 'center',
      overflow: 'hidden',
      borderRadius: '8px',
      background: 'url(none) #FAFFE6 no-repeat center 10px',
      borderWidth: '1px 1px 4px 1px',
      width: '100%',
      fontSize: '28px',
      lineHeight: '76px',
      borderStyle: 'solid',
      height: '120px',
    }">
      {{ title }}
    </div>

    <el-tabs tab-position="left" :style="{
      border: '2px solid #bad399',
      padding: '20px',
      margin: '20px 0 0 0',
      borderRadius: '8px',
      background: 'rgba(255,255,255,1)',
    }" @tab-click="handleClick">
      <el-tab-pane label="">
        <el-form class="center-preview-pv" ref="sessionForm" :model="sessionForm" :rules="rules" label-width="80px">
          <el-form-item :style="{ padding: '10px', margin: '0 0 4px', background: 'none' }"
            v-if="userTableName == 'users'" label="" prop="username">
            <el-input v-model="sessionForm.username" placeholder="" readonly></el-input>
          </el-form-item>
          <el-form-item :style="{ padding: '10px', margin: '0 0 4px', background: 'none' }"
            v-if="userTableName == 'users'" label="" prop="password">
            <el-input type="password" v-model="sessionForm.password" placeholder=""></el-input>
          </el-form-item>
          <el-form-item :style="{ padding: '10px', margin: '0 0 4px', background: 'none' }"
            v-if="userTableName == 'users'" label="" prop="name">
            <el-input v-model="sessionForm.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item :style="{ padding: '10px', margin: '0 0 4px', background: 'none' }"
            v-if="userTableName == 'users'" label="">
            <el-select v-model="sessionForm.gender" placeholder="">
              <el-option v-for="(item, index) in dynamicProp.gender" :key="index" :label="item"
                :value="item"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style="{ padding: '10px', margin: '0 0 4px', background: 'none' }"
            v-if="userTableName == 'users'" label="" prop="age">
            <el-input v-model="sessionForm.age" placeholder=""></el-input>
          </el-form-item>
          <el-form-item :style="{ padding: '10px', margin: '0 0 4px', background: 'none' }"
            v-if="userTableName == 'users'" label="" prop="telephone">
            <el-input v-model="sessionForm.telephone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item :style="{ padding: '10px', margin: '0 0 4px', background: 'none' }"
            v-if="userTableName == 'users'" label="">
            <el-upload class="avatar-uploader" :action="uploadUrl" :headers="headers" :show-file-list="false"
              :on-success="usersavatarHandleAvatarSuccess">
              <img :style="{
                border: '1px solid #bad399',
                cursor: 'pointer',
                minHeight: '100px',
                boxShadow: 'inset 0px 0px 48px 0px #FAFFE6',
                color: '#bad399',
                borderRadius: '6px',
                textAlign: 'center',
                width: '150px',
                fontSize: '32px',
                lineHeight: '100px',
                height: 'auto',
              }" v-if="sessionForm.avatar" :src="baseUrl + sessionForm.avatar" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item :style="{ padding: '0', margin: '16px 0 0 0' }">
            <el-button :style="{
              border: '0',
              cursor: 'pointer',
              padding: '0',
              margin: '0 20px 0 0',
              outline: 'none',
              color: '#fff',
              borderRadius: '40px',
              background: '#bad399',
              width: '128px',
              lineHeight: '40px',
              fontSize: '14px',
              height: '40px',
            }" type="primary" @click="onSubmit('sessionForm')"></el-button>
            <el-button :style="{
              border: '1px solid #bad399',
              cursor: 'pointer',
              padding: '0',
              margin: '0',
              outline: 'none',
              color: '#bad399',
              borderRadius: '40px',
              background: 'rgba(255, 255, 255, 1)',
              width: '128px',
              lineHeight: '40px',
              fontSize: '14px',
              height: '40px',
            }" type="danger" @click="logout"></el-button>
          </el-form-item>
        </el-form>

        <el-dialog title="" :visible.sync="dialogFormVisibleMoney" width="726px" center>
          <el-form :model="chongzhiForm">
            <el-form-item label="" label-width="120px">
              <el-input type="number" v-model="chongzhiForm.money" autocomplete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label-width="120px">
              <el-radio-group v-model="chongzhiForm.radio">
                <el-radio style="margin-bottom: 30px" label="">
                  <el-image style="width: 60px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/weixin.png')" fit="fill"></el-image>
                  <span style="display: inline-block; margin-left: 10px"></span>
                </el-radio>
                <el-radio label="">
                  <el-image style="width: 60px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/zhifubao.png')" fit="fill"></el-image>
                  <span style="display: inline-block; margin-left: 10px"></span>
                </el-radio>
                <el-radio label="">
                  <el-image style="width: 120px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/jianshe.png')" fit="fill"></el-image>
                </el-radio>
                <el-radio label="">
                  <el-image style="width: 126px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/nongye.png')" fit="fill"></el-image>
                </el-radio>
                <el-radio label="">
                  <el-image style="width: 140px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/zhongguo.png')" fit="fill"></el-image>
                </el-radio>
                <el-radio label="">
                  <el-image style="width: 120px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/jiaotong.png')" fit="fill"></el-image>
                </el-radio>
              </el-radio-group>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisibleMoney = false"> </el-button>
            <el-button type="primary" @click="chongzhi"></el-button>
          </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="dialogFormVisibleVip" width="726px" center>
          <el-form :model="chongzhiForm">
            <el-form-item label="" label-width="120px">
              <el-input readonly autocomplete="off" value="199/"></el-input>
            </el-form-item>
            <el-form-item label-width="120px">
              <el-radio-group v-model="chongzhiForm.radio">
                <el-radio style="margin-bottom: 30px" label="">
                  <el-image style="width: 60px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/weixin.png')" fit="fill"></el-image>
                  <span style="display: inline-block; margin-left: 10px"></span>
                </el-radio>
                <el-radio label="">
                  <el-image style="width: 60px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/zhifubao.png')" fit="fill"></el-image>
                  <span style="display: inline-block; margin-left: 10px"></span>
                </el-radio>
                <el-radio label="">
                  <el-image style="width: 120px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/jianshe.png')" fit="fill"></el-image>
                </el-radio>
                <el-radio label="">
                  <el-image style="width: 126px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/nongye.png')" fit="fill"></el-image>
                </el-radio>
                <el-radio label="">
                  <el-image style="width: 140px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/zhongguo.png')" fit="fill"></el-image>
                </el-radio>
                <el-radio label="">
                  <el-image style="width: 120px; height: 60px; vertical-align: middle"
                    :src="require('@/assets/jiaotong.png')" fit="fill"></el-image>
                </el-radio>
              </el-radio-group>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisibleVip = false"> </el-button>
            <el-button type="primary" @click="chongzhivip"></el-button>
          </div>
        </el-dialog>
      </el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
      <el-tab-pane label=""></el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import config from "@/config/config";
import Vue from "vue";
export default {
  //
  data() {
    return {
      title: "",
      baseUrl: config.baseUrl,
      sessionForm: {},
      rules: {},
      chongzhiForm: {
        money: "",
        radio: "",
      },
      disabled: false,
      dialogFormVisibleMoney: false,
      dialogFormVisibleVip: false,
      uploadUrl: config.baseUrl + "file/upload",
      imageUrl: "",
      headers: { Token: localStorage.getItem("Token") },
      userTableName: localStorage.getItem("UserTableName"),
      dynamicProp: {},
    };
  },
  created() {
    if ("users" == this.userTableName) {
      this.$set(this.sessionForm, "username", null);
    }
    if ("users" == this.userTableName) {
      this.$set(this.sessionForm, "password", null);
    }
    if ("users" == this.userTableName) {
      this.$set(this.sessionForm, "name", null);
    }
    if ("users" == this.userTableName) {
      this.$set(this.sessionForm, "gender", null);
    }
    if ("users" == this.userTableName) {
      this.$set(this.sessionForm, "age", null);
    }
    if ("users" == this.userTableName) {
      this.$set(this.sessionForm, "telephone", null);
    }
    if ("users" == this.userTableName) {
      this.$set(this.sessionForm, "avatar", null);
    }

    if ("users" == this.userTableName) {
      this.$set(this.rules, "username", [
        { required: true, message: "", trigger: "blur" },
      ]);
    }
    if ("users" == this.userTableName) {
      this.$set(this.rules, "password", [
        { required: true, message: "", trigger: "blur" },
      ]);
    }
    if ("users" == this.userTableName) {
      this.$set(this.rules, "name", [
        { required: true, message: "", trigger: "blur" },
      ]);
    }
    if ("users" == this.userTableName) {
      this.$set(this.rules, "age", [
        {
          required: false,
          validator: this.$validate.isIntNumer,
          trigger: "blur",
        },
      ]);
    }
    if ("users" == this.userTableName) {
      this.$set(this.rules, "telephone", [
        {
          required: false,
          validator: this.$validate.isMobile,
          trigger: "blur",
        },
      ]);
    }

    this.init();
    this.getSession();
  },
  //
  methods: {
    init() {
      if ("users" == this.userTableName) {
        this.dynamicProp.gender = ",".split(",");
      }
    },
    getSession() {
      this.$http
        .get(this.userTableName + "/session", { emulateJSON: true })
        .then((res) => {
          if (res.data.code == 0) {
            this.sessionForm = res.data.data;
            localStorage.setItem("userid", res.data.data.id);
            if (res.data.data.vip) {
              localStorage.setItem("vip", res.data.data.vip);
            }
            if (res.data.data.name) {
              localStorage.setItem("name", res.data.data.name);
            }
            if (res.data.data.avatar) {
              localStorage.setItem("headportrait", res.data.data.avatar);
            } else if (res.data.data.headportrait) {
              localStorage.setItem("headportrait", res.data.data.headportrait);
            }
          }
        });
    },
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http
            .post(this.userTableName + "/update", this.sessionForm)
            .then((res) => {
              if (res.data.code == 0) {
                this.$message({
                  message: "",
                  type: "success",
                  duration: 1500,
                });
              }
            });
        } else {
          return false;
        }
      });
    },
    usersavatarHandleAvatarSuccess(res, file) {
      if (res.code == 0) {
        if ("users" == this.userTableName) {
          this.sessionForm.avatar = "upload/" + res.file;
        }
      }
    },
    chongzhi() {
      if (this.chongzhiForm.money == "") {
        this.$message({
          message: "",
          type: "error",
          duration: 1500,
        });
        return;
      }
      if (this.chongzhiForm.money <= 0) {
        this.$message({
          message: "",
          type: "error",
          duration: 1500,
        });
        return;
      }
      if (this.chongzhiForm.radio == "") {
        this.$message({
          message: "",
          type: "error",
          duration: 1500,
        });
        return;
      }
      this.sessionForm.money =
        parseInt(this.sessionForm.money) + parseInt(this.chongzhiForm.money);
      this.$http
        .post(this.userTableName + "/update", this.sessionForm)
        .then((res) => {
          if (res.data.code == 0) {
            this.$message({
              message: "",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.dialogFormVisibleMoney = false;
              },
            });
          }
        });
    },
    chongzhivip() {
      this.chongzhiForm.money == 199;
      if (this.chongzhiForm.radio == "") {
        this.$message({
          message: "",
          type: "error",
          duration: 1500,
        });
        return;
      }
      if (this.sessionForm.vip == "") {
        this.$message({
          message: "",
          type: "success",
          duration: 1500,
        });
        return;
      }

      this.sessionForm.vip = "";
      this.$http
        .post(this.userTableName + "/update", this.sessionForm)
        .then((res) => {
          if (res.data.code == 0) {
            this.$message({
              message: "",
              type: "success",
              duration: 1500,
              onClose: () => {
                localStorage.setItem("vip", this.sessionForm.vip);
                this.dialogFormVisibleVip = false;
              },
            });
          }
        });
    },
    handleClick(tab, event) {
      switch (event.target.outerText) {
        case "":
          tab.$router.push("/index/center");
          break;
        case "":
          localStorage.setItem("storeupType", 1);
          tab.$router.push("/index/storeup");
          break;
        case "":
          // localStorage.setItem('storeupType', 1);
          tab.$router.push("/index/myticket");
          break;
        case "":
          // localStorage.setItem('storeupType', 1);
          tab.$router.push("/index/myhotel");
          break;
        case "":
          // localStorage.setItem('storeupType', 1);
          tab.$router.push("/index/myrestaurant");
          break;
        case "":
          // localStorage.setItem('storeupType', 1);
          tab.$router.push("/index/feedback");
          break;
      }

      this.title = event.target.outerText;
    },
    logout() {
      localStorage.clear();
      Vue.http.headers.common["Token"] = "";
      this.$router.push("/index/home");
      this.activeIndex = "0";
      localStorage.setItem("keyPath", this.activeIndex);
      this.$forceUpdate();
      this.$message({
        message: "",
        type: "success",
        duration: 1500,
      });
    },
  },
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.center-preview {
  .el-tabs {
    & ::v-deep .el-tabs__header {
      .el-tabs__nav-wrap {
        margin: 0;

        &::after {
          content: none;
        }
      }

      .el-tabs__active-bar {
        display: none !important;
      }
    }

    .center-preview-pv {
      .el-date-editor.el-input {
        width: auto;
      }

      .balance {
        .el-input {
          width: auto;
        }
      }
    }
  }
}

.center-preview .el-tabs ::v-deep .el-tabs__header {
  border: 2px solid #bad399;
  padding: 0;
  margin: 0 0px;
  background: none;
  width: 180px;
  position: relative;
  float: left;
}

.center-preview .el-tabs ::v-deep .el-tabs__header .el-tabs__item {
  padding: 0 10px;
  color: #333;
  background: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 40px;
  position: relative;
  text-align: center;
  height: 40px;
}

.center-preview .el-tabs ::v-deep .el-tabs__header .el-tabs__item:hover {
  padding: 0 10px;
  color: #666;
  background: #d4eddd;
  font-weight: 500;
  font-size: 14px;
  line-height: 40px;
  position: relative;
  text-align: center;
  height: 40px;
}

.center-preview .el-tabs ::v-deep .el-tabs__header .el-tabs__item.is-active {
  padding: 0 10px;
  color: #fff;
  background: #bad399;
  font-weight: 500;
  font-size: 14px;
  line-height: 40px;
  position: relative;
  text-align: center;
  height: 40px;
}

.center-preview .el-tabs ::v-deep .el-tabs__content .el-tab-pane {
  padding: 10px;
}

.center-preview-pv .el-form-item ::v-deep .el-form-item__label {
  padding: 0 10px 0 0;
  color: #666;
  font-weight: 500;
  width: 80px;
  font-size: 14px;
  line-height: 40px;
  text-align: right;
}

.center-preview-pv .el-form-item .el-form-item__content {
  margin-left: 80px;
}

.center-preview-pv .el-input ::v-deep .el-input__inner {
  border: 1px solid #bad399;
  border-radius: 4px;
  padding: 0 12px;
  box-shadow: inset 0px 0px 48px 0px #faffe6;
  outline: none;
  color: #666;
  background: rgba(255, 255, 255, 0.5);
  width: 400px;
  font-size: 14px;
  height: 40px;
}

.center-preview-pv .el-select ::v-deep .el-input__inner {
  border: 1px solid #bad399;
  border-radius: 4px;
  padding: 0 12px;
  box-shadow: inset 0px 0px 48px 0px #faffe6;
  outline: none;
  color: #666;
  background: rgba(255, 255, 255, 0.5);
  width: 400px;
  font-size: 14px;
  height: 40px;
}

.center-preview-pv .el-date-editor ::v-deep .el-input__inner {
  border: 1px solid #bad399;
  border-radius: 4px;
  padding: 0 10px 0 30px;
  box-shadow: inset 0px 0px 48px 0px #faffe6;
  outline: none;
  color: #666;
  background: rgba(255, 255, 255, 0.5);
  width: 200px;
  font-size: 14px;
  height: 40px;
}

.center-preview-pv ::v-deep .avatar-uploader-icon {
  border: 1px solid #bad399;
  cursor: pointer;
  border-radius: 6px;
  box-shadow: inset 0px 0px 48px 0px #faffe6;
  color: #bad399;
  width: 150px;
  font-size: 32px;
  min-height: 100px;
  line-height: 100px;
  text-align: center;
  height: auto;
}

.center-preview-pv .el-form-item.balance ::v-deep .el-input__inner {
  border: 1px solid #bad399;
  border-radius: 4px 0 0 4px;
  padding: 0 12px;
  box-shadow: inset 0px 0px 48px 0px #faffe6;
  outline: none;
  margin: 0 10px 0 0;
  color: #666;
  background: rgba(255, 255, 255, 0.5);
  display: inline-block;
  width: 200px;
  font-size: 14px;
  height: 40px;
}
</style>
