<style rel="stylesheet/scss" lang="scss" scoped>
.container {
  position: relative;

  .el-form-item {
    & ::v-deep .el-form-item__content {
      width: 100%;
    }
  }

  .list-item ::v-deep .el-input .el-input__inner {
    border: 1px solid rgba(204, 204, 204, 1);
    border-radius: 6px;
    padding: 0 10px;
    color: rgba(153, 153, 153, 1);
    width: 100%;
    font-size: 18px;
    outline-offset: 4px;
    height: 44px;
  }

  .list-code ::v-deep .el-input .el-input__inner {
    border-radius: 6px;
    padding: 0 10px;
    outline: none;
    color: rgba(153, 153, 153, 1);
    display: inline-block;
    vertical-align: middle;
    width: calc(100% - 160px);
    font-size: 16px;
    border-color: rgba(204, 204, 204, 1);
    border-width: 1px;
    border-style: solid;
    height: 44px;
  }

  .list-type ::v-deep .el-radio__input .el-radio__inner {
    border-color: #666666;
  }
  .list-type ::v-deep .el-radio__input.is-checked .el-radio__inner {
    background: rgba(40, 40, 40, 1);
    border-color: rgba(40, 40, 40, 1);
  }
  .list-type ::v-deep .el-radio__label {
    color: #999;
    font-size: 14px;
  }
  .list-type ::v-deep .el-radio__input.is-checked + .el-radio__label {
    color: rgba(40, 40, 40, 1);
    font-size: 14px;
  }
}
</style>
